

.button {
    border-radius: 30px;
    padding: 0.75rem 3.5rem;
    font: inherit;
    border: 1px solid #4f005f;
    background: #4f005f;
    color: white;
    cursor: pointer;

    
}

.button:focus {
    outline: none;
}

.button:hover,
.button:active {
  background: #741188;
  border-color: #741188;
}

button:disabled,
button:focus:disabled,
button:hover:disabled,
button:active:disabled {
  background: #ccc;
  border-color: #ccc;
  color: #666666;
  cursor: not-allowed;
}

@media (max-width: 565px) {

  .button {
      padding: 0.45rem 1.5rem;
  }
}